import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { FirebaseServiceProvider } from '../providers/firebase-service/firebase-service';
import { HttpClientModule } from '@angular/common/http';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contacts/contacts.module.ngfactory#ContactsPageModuleNgFactory', name: 'ContactsPage', segment: 'contacts', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/job-experiences/job-experiences.module.ngfactory#JobExperiencesPageModuleNgFactory', name: 'JobExperiencesPage', segment: 'job-experiences', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/products/products.module.ngfactory#ProductsPageModuleNgFactory', name: 'ProductsPage', segment: 'products', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/technology-skills/technology-skills.module.ngfactory#TechnologySkillsPageModuleNgFactory', name: 'TechnologySkillsPage', segment: 'technology-skills', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FirebaseServiceProvider
  ]
})
export class AppModule {}



